body {
  font-family: "Roboto", sans-serif;
  background-color: #dccaf8;
  margin: 0;
  padding: 0;
}

.dashboard-container {
  display: flex;
  height: auto;
}

.dashboard-sidebar {
  width: 230px;
  background-color: #b4a7e9;
  height: 100vh;
  top: 0;
  position: fixed;
  justify-content: space-between;
  flex-direction: column;
}

.dashboard-sidebar .menu-item {
  margin-bottom: 15px;
  cursor: pointer;
}

.dashboard-main {
  margin-left: 17.5%;
  flex-grow: 1;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}

.dashboard-header {
  background-color: #A294F9;
  color: black;
  padding: 1px;
  display: flex;
  min-height: 50px;
  justify-content: space-between;
  backdrop-filter: blur(10px); 
  background: rgba(255, 255, 255, 0.2);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.2);
  transition: transform 0.5s ease, opacity 0.5s ease;
}

.dashboard-header .menu-icon {
  display: none;
}

/* Dashboard Main Content */
.dashboard-content {
  margin-top: 10px;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.dashboard-title {
  font-size: 24px;
  color: #A294F9;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.dashboard-LastCreateTable {
  margin-top: 10px;
  height: 100vh;
  width: 100%;
}

.dashboard-sidebar .menu-item:hover {
  background-color: #A294F9;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}
h2 {
  font-size: 16px;
  color: #000000;
}

@media (max-width: 1300px) {
  .dashboard-sidebar {
    /* width: 100px; */
    /* position: fixed; */
    height: 100%;
    top: 0;
    transition: width 0.3s ease;
  }
  .dashboard-sidebar.open {
    width: 240px;
  }

  .dashboard-sidebar .menu-item {
    display: none;
  }

  .dashboard-sidebar.open .menu-item {
    display: block;
  }

  .dashboard-main {
    margin-left: 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
  }

  .dashboard-header .menu-icon {
    display: block;
    color: #000000;
  }
}
