/* General Page Styling */
.container {
    width: 100%;
    margin: auto;
    font-family: Arial, sans-serif;
  }
  
  h2 {
    text-align: center;
    color: #333;
    margin-bottom: 20px;
  }
  
  h3 {
    text-align: center;
    color: #333;
  }
  
  .form {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: space-evenly;
    width: 95%;
  }
  
  .form-group {
    margin-bottom: 15px;
    width: 30%;
  }
  
  label {
    display: block;
    margin-bottom: 5px;
    font-size: 12px;
  }
  
  input,
  select {
    width: 100%;
    padding: 8px;
    font-size: 14px;
    margin-top: 5px;
    border-radius: 4px;
    border: 1px solid #ccc;
  }
  
  
  .btn-submit {
    background-color: #b4a7e9;
    color: rgb(0, 0, 0);
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 14px;
    width: 15%;
  }
  .save-btn-submit{
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 14px;
    width: 15%;
    background-color: #b4a7e9;
    color: rgb(0, 0, 0);
    margin: 10px;
    align-items: flex-end;
  }
  /* .btn-submit:hover {
    background-color: #caa6f1;
  } */
  
  .btn-edit {
    background-color: #28a745;
    color: black;
    margin-right: 10px;width: 100px;
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 14px;
    width: 48%;
  }
  
  .btn-edit:hover {
    background-color: #218838;
  }
  
  .btn-delete {
    background-color: #dc3545;
    color: black;
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 14px;
    width: 48%;
  }
  
  .btn-delete:hover {
    background-color: #c82333;
  }
  
  .error {
    color: red;
    font-size: 12px;
  }
  
  .table-wrapper {
    overflow-x: auto;
    width: 95%;
    margin-top: 20px;
  }
  
  /* table style */
  .table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .table th,
  .table td {
    border: 1px solid #A294F9;
    padding: 8px;
    text-align: left;
    color: #333;
  }
  
  .table th {
    background-color: #b4a7e9;
    font-weight: bold;
    
  }
  
  .table td {
    font-size: 14px;
    background-color: #eee9ff;
  }
  @media (max-width: 1300px) {
    .form-group {
        margin-bottom: 15px;
        width: 45%;
      }
}
  
@media (max-width: 850px) {
    .form-group {
        margin-bottom: 15px;
        width: 100%;
      }
      button {
        padding: 10px 20px;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        font-size: 14px;
        width: 25%;
      }
      .table-wrapper {
        overflow-x: scroll; /* Enables horizontal scroll */
      }
    
      .table {
        min-width: 100%; /* Ensures the table is wide enough to require scrolling */
      }
}