  /* Error Popup Styling */
.error-popup {
  position: fixed;
  top: 20px;
  right: 20px;
  background-color: #f8d7da;
  color: #721c24;
  padding: 15px;
  border-radius: 5px;
  border: 1px solid #f5c6cb;
  z-index: 9999;
  font-size: 14px;
}

.error-popup button {
  background-color: transparent;
  color: #721c24;
  border: none;
  font-weight: bold;
  cursor: pointer;
  padding: 5px;
  margin-left: 10px;
}

.close-popup {
  margin-top: 3%;
  padding: 10px 15px;
  font-size: 14px;
  color: black;
  background-color: #f44336;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-right: 10px;
  width: 100%;
  box-sizing: border-box;
  /* margin-bottom: 15px; */
  text-align: center;
}

.close-popup:hover {
  background-color: #d32f2f;
}

/* Add this to your existing CSS file (e.g., Master.css) */
.hand_cash_popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; /* Ensure it's above other content */
}

.popup-content {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  width: 300px; /* Adjust width as needed */
  text-align: center;
}

.popup-content h3 {
  margin-bottom: 15px;
}

.popup-content input {
  width: 100%;
  padding: 8px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.popup-content button {
  margin: 5px;
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  background-color: #b4a7e9;
  color: black;
  width: 50%;
}

.popup-content button:hover {
  background-color: #b4a7e9;
}