/* styles.css */
body {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 0;
    background-color: rgb(231, 217, 255);
}





.bill-no-container {
  display: flex;
  align-items: center;
}

.bill-btn {
  padding: 5px 10px;
  margin: 0 5px;
  font-size: 18px;
  background-color: #f0f0f0;
  border: 1px solid #ccc;
  cursor: pointer;
}

.bill-btn:disabled {
  cursor: not-allowed;
  background-color: #ddd;
}


h1 {
    text-align: center;
    color: #333;
    margin-top: 20px;
}
.head-form {
   /* height: 70px; */
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    align-content: center;
}
#h1{
  margin: 0px;
  font-size: 16px;
  text-align: left;
  margin-left: 5%;
  padding: 10px;
}
.sub-form{
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    align-content: center;
    margin: 10px;
}
label {
    display: block;
    font-weight: bold;
    color: #555;
    width: 100%;
    margin-bottom: 5px;
    
}
select {
    width: 100%;
    padding: 8px;
    font-size: 14px;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin-bottom: 3px;      
}
input{
    width: 100%;
    padding: 8px;
    font-size: 14px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

input[type="number"] {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

input:focus, select:focus {
    outline: none;
    border-color: #b4a7e9;
}



.Submit-btn:hover {
    background-color: #45a049;
}

.btn-edit {
  color: #28a745;
  margin-right: 10px;width: 100px;
}

.btn-edit:hover {
  background-color: #b8e4c1;
}

.btn-delete {
  color: #dc3545;
  width: 100px;
}

.btn-delete:hover {
  background-color: #f1c2c7;
}

.error {
  color: red;
  font-size: 12px;
}

.error {
    color: red;
    font-size: 12px;
    /* margin-top: 5px; */
}

input.error, select.error {
    border-color: red;
}


/* Style the table container */
.table-container {
  width: 98%;
  overflow-x: auto;   margin-bottom: 20px;
}

.table_wrapper {
  overflow-x: auto;
  width: 200%;
  margin-top: 20px;
  height: auto;
}

.table {
  width: 98%;
  border-collapse: collapse;

}
.th{
  background-color: #b4a7e9;
  font-weight: bold;
  text-align: center;
  font-size: 14px;
  height: 35px;
  width: 2%;
  border: 1px solid #ddd;
  color: black;
}

td {
  word-wrap: break-word; 
  border: 1px solid #ddd;
}

.form-actions{
  margin-top: 20px;
}

.headformdiv{
    width: 17%;
    height: 60px;
}

.subformdiv{
    width: 15%;
    padding: 1%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}


.table {
  width: 100%;
  border-collapse: collapse;
}

.table th,
.table td {
  border: 1px solid #A294F9;
  padding: 8px;
  text-align: center;
  white-space: nowrap;
}

.table th {
  font-weight: bold;
  
}
.table-div{
  overflow-x: auto;
}
.table td {
  font-size: 14px;
}

.subformdiv,
.subbuttondiv,
.headbuttondiv{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 10px;
}


.subformdiv,
.subbuttondiv,
.headbuttondiv{
    flex: 0 1 15%;
    min-width: 150px;
}

@media (max-width: 1678px) {
