}
  
@media (max-width: 850px) {
    .form-group {
        margin-bottom: 15px;
        width: 100%;
      }
      button {
        padding: 10px 20px;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        font-size: 14px;
        width: 25%;
      }
      .table-wrapper {
        overflow-x: scroll; /* Enables horizontal scroll */
      }
    
      .table {
        min-width: 100%; /* Ensures the table is wide enough to require scrolling */
      }
