.container_payment {
  display: flex;
  flex-wrap: wrap;
  align-content: stretch;
  justify-content: space-evenly;
  align-items: center;
}
.Div_Pament {
  width: 200px;
  margin: 2%;
}
.header {
  margin: 5px;
  font-size: 17px;
  padding-left: 35px;
}
.payment-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-left: 3%;
  margin-right: 12%;
}
.Div_Pament_radio {
  display: flex;
  justify-content: space-around;
  width: 200px;
  margin: 2%;
  align-items: center;
}

.edit {
  color: #28a745;
  margin-right: 10px;
  width: 100px;
}

.edit:hover {
  background-color: #b8e4c1;
}

.delete {
  color: #dc3545;
  width: 100px;
}

.delete:hover {
  background-color: #f1c2c7;
}

.error-popup {
  position: fixed;
  /* top: 20%; */
  /* left: 76%; */
  /* transform: translateX(-50%); */
  background-color: #f8d7da;
  color: #721c24;
  padding: 15px;
  border-radius: 5px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.error-popup button {
  background-color: #721c24;
  color: white;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
  font-size: 14px;
  margin: 15px;
}
