@media print {
  @page {
    size: A4 landscape;
  }

  body {
    -webkit-print-color-adjust: exact;
    font-size: 12px;
  }

  #printable-area {
    width: 100%;
    overflow: hidden;
  }

  #print-table {
    width: 98%;
    border-collapse: collapse;
    font-size: 12px;
  }

  .printable-table th,
  .printable-table td {
    padding: 5px;
    text-align: center;
  }

  button {
    display: none;
  }
  #table_head1 {
    display: none;
  }
}

#printStatement {
  background-color: #b4a7e9;
  color: black;
  margin-left: 5%;
  margin-top: 2%;
  height: 35px;
  width: 25%;
  border: 1px solid #ddd;
  border-radius: 10px;
}