.loginBody {
  font-family: "Arial", sans-serif;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: linear-gradient(135deg, rgb(231, 217, 255), rgb(180, 167, 233));
}

.login-container {
  display: flex;
  width: 800px;
  background: linear-gradient(135deg, rgb(231, 217, 255), rgb(180, 167, 233));
  border-radius: 8px;
  overflow: hidden;
  height: 350px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  align-items: center;
}

.welcome-section {
  flex: 1;
  padding: 40px;
  color: white;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  position: relative;
}

.welcome-section h1 {
  font-size: 2rem;
  margin: 0;
}

.social-login {
  display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      color: black;
      font-size: 11px;
      margin: 16%;
      margin-bottom: -40px;
}


.login-section {
  flex: 1;
  padding: 40px;
}

.login-section h2 {
  margin-top: 0;
}

.login_from {
  display: flex;
  flex-direction: column;
}

input[type="text"],
input[type="password"] {
  padding: 10px;
  margin: 10px 0;
  border-radius: 5px;
  border: 1px solid #ccc;
  width: 93%;
}

.remember-forgot {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.login-button {
  padding: 10px;
  background: #00c853;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 20px;
}
