body{
  color: black;
}
.container {
    width: 98%;
  }
  
  .BS-head{
    display: flex;
        justify-content: space-between;
        align-items: center;
        align-content: center;
        flex-wrap: wrap;
        /* margin: 7px; */
        padding-right: 30px;
        padding-left: 30px;
  }
 
  
  .btn {
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .btn-add {
    background-color: #b4a7e9;
    color: black;
  }
  
  .btn-edit {
    background-color: #28a745;
    color: black;
    margin-right: 10px;
  }
  
  .btn-delete {
    background-color: #dc3545;
    color: black;
    margin-right: 10px;
  }
  
  .btn-generate-bill {
    background-color: #b4a7e9;
    color: black;
  }
  .btn-Show-bill{
    background-color: #b4a7e9;
    color: rgb(0, 0, 0);
    width: 100%;
  }

  .p{
    text-align: center;
    font-size: 12px;
  }
  table {
    width: 96%;
    border-collapse: collapse;
    /* margin-top: 20px; */
    /* padding: 12px; */
    margin: 15px;
    margin-bottom: 10px;
  }
  
  td {
    padding: 5px;
    text-align: center;
    font-size: 13px;
    background-color: #eee9ff;
    height: 35px;
  }
  
  th {
     background-color: #b4a7e9;
    font-weight: bold;
    border: 1px solid #F5EFFF;
    color: #000000;
    text-align: center;
    font-size: 14px;
  }
  
  
  
  /* Styling for checkbox in each row */
  input[type="checkbox"] {
    margin-right: 10px;
  }
  
  /* Paragraph styles */
  p {
    margin-top: 10px;
  }
  
  /* Add some spacing around the sections */
  section {
    margin-bottom: 40px;
  }
  .section-header{
    text-align: left;
    font-size: 12px;
    /* border-bottom: 2px solid; */
    padding: 10px;
    width: 98%;
    cursor: pointer;
    color: black;
    background-color: #b4a7e9;
  }
