/* Container for the entire index */
.container_index {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    font-family: 'Arial', sans-serif;
    color: #333;
  }
  
  /* Container for the main title */
  .container_box {
    width: 100%;
    margin-bottom: 20px;
    padding: 20px;
    background-color: #eee9ff;
    border-radius: 10px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    display: flex;
    justify-content: space-between;
    /* align-items: center; */
    flex-direction: column;
  }
  
  /* Title styling */
  .title {
    font-size: 2rem;
    color: #2d3d44;
    font-style: oblique;
    /* margin: 0px; */
  }
  
  /* Styling for the date and time section */
  .container_box > div {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1rem;
    color: #555;
  }
  
  /* Bill check section box */
  .Bill_check_box {
    background-color: #eee9ff;
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 8px;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
    text-align: center;
    font-size: 1.2rem;
    padding-left: 20px;
    padding-right: 20px;
  }
  .Bill_button_box{
    display: flex;
        justify-content: center;
        align-items: center;
    
  }
  /* Button styling for view action */
  #Bill_button {
    font-size: 1rem;
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    color: black;
    background-color: #b4a7e9;
    width: 100%;
  }
  
  /* Responsive design adjustments */
  @media (max-width: 768px) {
    .container_index {
      padding: 10px;
    }
  
    .container_box {
      padding: 15px;
    }
  
    .title {
      font-size: 1.5rem;
    }
  
    .container_box > div {
      flex-direction: column;
      text-align: center;
    }
  
    .Bill_check_box {
      font-size: 1rem;
      padding: 10px;
    }
  
    #printStatement {
      font-size: 1rem;
      padding: 8px;
    }
  }
  

  /* For better spacing of items */
  h6.h6_Box {
    font-size: 1.5rem;
    font-weight: 500;
    text-align: center;
    margin-bottom: 10px;    margin: 10px;
  }
  
  h5 {
    font-size: 1.1rem;
    margin: 8px 0;
    font-weight: 400;
    text-align: center;
  }
  